<div class="DraggableTable"><a-spin :spinning="loading"><table class="drag-table" :class="bordered?&quot;has-border-top-left&quot;:&quot;no-border-top&quot;" :boolean="bordered"><thead :style="tHeadStyle"><tr class="drag-thead-tr"><th class="drag-th td checkbox" v-if="rowSelect" :class="bordered?&quot;has-border-bottom-right&quot;:&quot;no-border-bottom&quot;"><a-checkbox v-model:checked="isAllCheck" @change="checkAll"></a-checkbox></th><th class="drag-th td" v-for="item in columns" :key="item.key" :class="{ellipsis: item.ellipsis}, bordered?&quot;has-border-bottom-right&quot;:&quot;no-border-bottom&quot;" :style="{&quot;text-align&quot;: item.align || &quot;left&quot;, &quot;width&quot;: item.width || &quot;auto&quot;, &quot;flex&quot;: `${item.width ? &quot;none&quot; : 1}`}">{{item.title}}</th></tr></thead><Draggable v-if="dragData.length !== 0" v-model="dragData" tag="tbody" @end="onDragEnd" item-key="uid"><template #item="{ element,index }"><tr class="drag-item tr cursor-move" :key="element.uid"><td class="td checkbox" v-if="rowSelect" :class="bordered?&quot;has-border-bottom-right&quot;:&quot;no-border-bottom&quot;"><a-checkbox v-model:checked="element.isChecked" @change="checkChange(element, index)"></a-checkbox></td><template v-for="col in columns" :key="col.dataIndex"><td class="td" :class="{ellipsis: col.ellipsis}, bordered?&quot;has-border-bottom-right&quot;:&quot;no-border-bottom&quot;" :style="{&quot;text-align&quot;: col.align || &quot;left&quot;, &quot;width&quot;: `${col.width} !important` || &quot;auto&quot;, &quot;flex&quot;: `${col.width ? &quot;none&quot; : 1}`}" :title="col.slots ? &quot;&quot; : element[col.dataIndex]"><slot v-if="col.slots" :name="col.slots.customRender" :record="element" :index="index"></slot><span v-else>{{element[col.dataIndex]}}</span></td></template></tr></template></Draggable><tbody v-else><tr><td class="border-right-bottom" :colspan="columns.length + (rowSelect ? 1 : 0)"><a-empty :image="simpleImage"></a-empty></td></tr></tbody></table></a-spin></div>